import { Component, Prop, h } from '@stencil/core';
import { Editor } from '../../editor';
import { fontSizes } from '../../extensions/font-size';
import { lineHeights } from '../../extensions/line-height';
import { textColors } from '../../extensions/color';
import { fontFamilies } from '../../extensions/font-family';
import { Level as HeadingLevel } from '@tiptap/extension-heading';

const headingLevels = [1, 2, 3, 4];

type MenuItem = {
  icon?: string;
  title?: string;
  action?: () => void;
  isActive?: () => boolean;
  isDisabled?: () => boolean;
  isHidden?: () => boolean;
  menuItems?: MenuItem[];
  label?: string;
  flip?: 'v' | 'h';
  type?: 'divider';
};

@Component({
  tag: 'tiptap-menubar',
  styleUrl: 'tiptap-menubar.scss',
})
export class TiptapMenubar {
  /**
   * Tiptap editor instance.
   */
  @Prop() editor: Editor;

  /**
   * Force update counter. Forces a re-render of the menu.
   */
  @Prop() forceUpdateCounter: number;

  /**
   * Editor states. Forces a re-render of the menu.
   */
  @Prop() states: any;

  /**
   * Toggle monaco callback. Toggles the monaco editor.
   */
  @Prop() toggleMonaco: Function;

  /**
   * Toggle fullscreen callback.
   */
  @Prop() toggleFullscreen: Function;

  getMonacoToggler(): MenuItem[][] {
    return [[
      {
        icon: 'ci-edit_pencil_line_01',
        title: 'Source Code Mode',
        action: () => this.toggleMonaco?.(),
        isActive: () => this.states?.isMonaco,
        isHidden: () => this.states?.isCollaborative,
      }
    ]];
  }

  getMenuItems(editor: Editor): MenuItem[][] {
    return [
      [
        {
          icon: 'ci-text_align_left',
          title: 'Align Left',
          action: () => editor.chain().focus().setTextAlign('left').run(),
          isActive: () => editor.isActive({ textAlign: 'left' }),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-text_align_center',
          title: 'Center',
          action: () => editor.chain().focus().setTextAlign('center').run(),
          isActive: () => editor.isActive({ textAlign: 'center' }),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-text_align_right',
          title: 'Align Right',
          action: () => editor.chain().focus().setTextAlign('right').run(),
          isActive: () => editor.isActive({ textAlign: 'right' }),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-text_align_justify',
          title: 'Justify',
          action: () => editor.chain().focus().setTextAlign('justify').run(),
          isActive: () => editor.isActive({ textAlign: 'justify' }),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
      ], [
        {
          icon: 'ci-text',
          title: 'Font Family',
          menuItems: Object.keys(fontFamilies).map(font => ({
            label: font,
            action: () => editor.chain().focus().setFontFamily(fontFamilies[font]).run(),
            isActive: () => editor.isActive('textStyle', { fontFamily: fontFamilies[font] }),
          })),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-font',
          title: 'Font Size',
          menuItems: fontSizes.map((size) => ({
            label: `${size}px`,
            action: () => editor.chain().focus().setFontSize(size).run(),
            isActive: () => editor.isActive({ fontSize: size }),
          })),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        // {
        //   icon: 'ci-expand',
        //   title: 'Enlarge Font Size',
        //   action: () => editor.chain().focus().scaleFontSize('upscale').run(),
        //   isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        // },
        // {
        //   icon: 'ci-shrink',
        //   title: 'Reduce Font Size',
        //   action: () => editor.chain().focus().scaleFontSize('downscale').run(),
        //   isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        // },
        {
          icon: 'ci-swatches_palette',
          title: 'Text Color',
          menuItems: [...textColors.map(color => ({
            icon: `ci-text color color-${color.toLowerCase()}`,
            title: color.replace(/([A-Z])/g, ' $1').trim(),
            action: () => editor.chain().focus().setColor(color).run(),
            isActive: () => editor.isActive('textStyle', { color }),
          })), {
            icon: 'ci-close_md',
            title: 'Clear Color',
            action: () => editor.chain().focus().unsetColor().run(),
          }],
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-bold',
          title: 'Bold',
          action: () => editor.chain().focus().toggleBold().run(),
          isActive: () => editor.isActive('bold'),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-italic',
          title: 'Italic',
          action: () => editor.chain().focus().toggleItalic().run(),
          isActive: () => editor.isActive('italic'),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-underline',
          title: 'Underline',
          action: () => {
            if (editor.isActive('strike')) {
              editor.chain().focus().toggleStrike().run();
            } else {
              editor.chain().focus().toggleUnderline().run();
            }
          },
          isActive: () => editor.isActive('underline') || editor.isActive('strike'),
          menuItems: [
            {
              icon: 'ci-underline',
              title: 'Underline',
              action: () => editor.chain().focus().toggleUnderline().run(),
              isActive: () => editor.isActive('underline'),
            },
            {
              icon: 'ci-strikethrough',
              title: 'Strike',
              action: () => editor.chain().focus().toggleStrike().run(),
              isActive: () => editor.isActive('strike'),
            }
          ],
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-bulb',
          title: 'Highlight',
          action: () => editor.chain().focus().toggleHighlight().run(),
          isActive: () => editor.isActive('highlight'),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-link',
          title: 'Link',
          action: () => {
            const prevHref = editor.isActive('link') ? editor.getAttributes('link').href : '';
            const href = prompt('Enter URL', prevHref);
            editor.chain().focus().setLink({ href }).run();
          },
          isActive: () => editor.isActive('link'),
          menuItems: [
            {
              icon: 'ci-link',
              title: 'Link',
              action: () => {
                const prevHref = editor.isActive('link') ? editor.getAttributes('link').href : '';
                const href = prompt('Enter URL', prevHref);
                editor.chain().focus().setLink({ href }).run();
              },
            },
            {
              icon: 'ci-link_break',
              title: 'Unlink',
              action: () => editor.chain().focus().unsetLink().run(),
              isDisabled: () => !editor.isActive('link'),
            }
          ],
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
        {
          icon: 'ci-menu_duo_md',
          title: 'Line Spacing',
          menuItems: lineHeights.map((lineHeight) => ({
            label: `${lineHeight}x`,
            action: () => editor.chain().focus().setLineHeight(lineHeight).run(),
            isActive: () => editor.isActive({ lineHeight }),
          })),
          isDisabled: () => !(editor.isActive('paragraph') || editor.isActive('heading')),
        },
      ], [
        {
          icon: 'ci-heading',
          title: 'Heading',
          isActive: () => editor.isActive('heading'),
          menuItems: headingLevels.map((level) => ({
            icon: `ci-heading_h${level}`,
            title: `Heading ${level}`,
            action: () => editor.chain().focus().toggleHeading({ level: level as HeadingLevel }).unsetFontSize().run(),
            isActive: () => editor.isActive('heading', { level }),
          })),
        },
        {
          icon: 'ci-paragraph',
          title: 'Paragraph',
          action: () => editor.chain().focus().setParagraph().run(),
          isActive: () => editor.isActive('paragraph'),
        },
        {
          icon: 'ci-code',
          title: 'Code',
          action: () => {
            if (editor.isActive('code')) {
              editor.chain().focus().toggleCode().run();
            } else {
              editor.chain().focus().toggleCodeBlock().run();
            }
          },
          isActive: () => editor.isActive('code') || editor.isActive('codeBlock'),
          menuItems: [
            {
              icon: 'ci-window_code_block',
              title: 'Code Block',
              action: () => editor.chain().focus().toggleCodeBlock().run(),
              isActive: () => editor.isActive('codeBlock'),
            },
            {
              icon: 'ci-code',
              title: 'Code',
              action: () => editor.chain().focus().toggleCode().run(),
              isActive: () => editor.isActive('code'),
            },
          ],
        },
        // {
        //   icon: 'ci-chevron_up',
        //   title: 'Superscript',
        //   action: () => editor.chain().focus().toggleSuperscript().run(),
        //   isActive: () => editor.isActive('superscript'),
        // },
        // {
        //   icon: 'ci-chevron_down',
        //   title: 'Subscript',
        //   action: () => editor.chain().focus().toggleSubscript().run(),
        //   isActive: () => editor.isActive('subscript'),
        // },
      ], [
        {
          icon: 'ci-list_unordered',
          title: 'Bullet List',
          action: () => editor.chain().focus().toggleBulletList().run(),
          isActive: () => editor.isActive('bulletList'),
        },
        {
          icon: 'ci-list_ordered',
          title: 'Ordered List',
          action: () => editor.chain().focus().toggleOrderedList().run(),
          isActive: () => editor.isActive('orderedList'),
        },
        {
          icon: 'ci-list_checklist',
          title: 'Task List',
          action: () => editor.chain().focus().toggleTaskList().run(),
          isActive: () => editor.isActive('taskList'),
        },
        {
          icon: 'ci-image_02',
          title: 'Image',
          action: () => {
            const input = document.createElement('input');
            input.setAttribute('type', 'file');
            input.setAttribute('accept', 'image/jpeg,image/gif,image/png,image/jpg');
            input.onchange = e => {
              const { files } = e.target as any;
              if (Boolean((files as FileList).length)) {
                editor
                  .chain()
                  .focus()
                  .uploadImage((files as FileList).item(0))
                  .run();
              }
            };
            input.click();
          },
        },
      ], [
        {
          icon: 'ci-double_quotes_l',
          title: 'Blockquote',
          action: () => editor.chain().focus().toggleBlockquote().run(),
          isActive: () => editor.isActive('blockquote'),
        },
        {
          icon: 'ci-remove_minus',
          title: 'Horizontal Rule',
          action: () => editor.chain().focus().setHorizontalRule().run(),
        },
      ], [
        {
          icon: 'ci-table',
          title: 'Table',
          action: () => editor.chain().focus().insertTable({ rows: 4, cols: 3, withHeaderRow: false }).run(),
          isDisabled: () => editor.isActive('table'),
        },
        {
          icon: 'ci-combine_cells',
          title: 'Merge Table Cells',
          action: () => editor.chain().focus().mergeOrSplit().run(),
          isActive: () => editor.can().splitCell(),
          isDisabled: () => !editor.can().mergeOrSplit(),
        },
        {
          icon: 'ci-add_row',
          flip: 'v',
          title: 'Insert Table Row Before',
          action: () => editor.chain().focus().addRowBefore().run(),
          isDisabled: () => !editor.can().addRowBefore(),
        },
        {
          icon: 'ci-add_row',
          title: 'Insert Table Row After',
          action: () => editor.chain().focus().addRowAfter().run(),
          isDisabled: () => !editor.can().addRowAfter(),
        },
        {
          icon: 'ci-add_column',
          flip: 'h',
          title: 'Insert Table Column Before',
          action: () => editor.chain().focus().addColumnBefore().run(),
          isDisabled: () => !editor.can().addColumnBefore(),
        },
        {
          icon: 'ci-add_column',
          title: 'Insert Table Column After',
          action: () => editor.chain().focus().addColumnAfter().run(),
          isDisabled: () => !editor.can().addColumnAfter(),
        },
        {
          icon: 'ci-delete_row',
          title: 'Delete Table Row',
          action: () => editor.chain().focus().deleteRow().run(),
          isDisabled: () => !editor.can().deleteRow(),
        },
        {
          icon: 'ci-delete_column',
          title: 'Delete Table Column',
          action: () => editor.chain().focus().deleteColumn().run(),
          isDisabled: () => !editor.can().deleteColumn(),
        },
        // {
        //   icon: 'ci-heading',
        //   title: 'Toggle Table Heading',
        //   action: () => editor.chain().focus().toggleHeaderRow().run(),
        //   isDisabled: () => !editor.can().toggleHeaderRow(),
        // },
        {
          icon: 'ci-table_remove',
          title: 'Delete Table',
          action: () => editor.chain().focus().deleteTable().run(),
          isDisabled: () => !editor.can().deleteTable(),
        },
      ], [
        // {
        //   icon: 'ci-arrow_sub_down_left',
        //   title: 'Hard Break',
        //   action: () => editor.chain().focus().setHardBreak().run(),
        // },
        {
          icon: 'ci-close_circle',
          title: 'Clear Format',
          action: () => editor.chain().focus().clearNodes().unsetAllMarks().run(),
        },
      ], [
        {
          icon: 'ci-undo',
          title: 'Undo',
          action: () => editor.chain().focus().undo().run(),
          isDisabled: () => !editor.can().undo(),
        },
        {
          icon: 'ci-redo',
          title: 'Redo',
          action: () => editor.chain().focus().redo().run(),
          isDisabled: () => !editor.can().redo(),
        },
      ], [
        {
          icon: 'ci-bar_top',
          title: 'Toggle Fullscreen',
          action: () => this.toggleFullscreen?.(),
          isActive: () => this.states?.isFullscreen,
          isHidden: () => !Boolean(this.toggleFullscreen),
        },
      ], ...this.getMonacoToggler(),
    ];
  };

  render() {
    const { editor, states } = this;
    const { isMonaco } = states;

    const menuItems = isMonaco ? this.getMonacoToggler() : this.getMenuItems(editor);

    const menuView = menuItems.map(itemGroup => {
      const items = itemGroup.map(item => {
        const { isHidden, ...itemProps } = item;
        return isHidden && isHidden() ? null : <tiptap-menu-item itemProps={{ ...itemProps }} />;
      });
      
      return (
        <div class="menu-item-group">
          {items}
        </div>
      );
    });

    return (
      <div class="editor__header">
        {menuView}
      </div>
    );
  }
}
