import { Component, Prop, Fragment, Listen, h } from '@stencil/core';
import { Editor } from '../../editor';

@Component({
  tag: 'tiptap-editor-content',
  styleUrl: 'tiptap-editor-content.scss',
})
export class TiptapEditorContent {
  /**
   * Tiptap editor instance.
   */
  @Prop() editor: Editor;

  private editorContentRef!: HTMLDivElement;

  @Listen('click', { capture: true })
  handleClick() {
    this.editor.chain().focus().run();
  }

  private init() {
    const { editor } = this;

    if (Boolean(editor) && Boolean(editor.options.element)) {
      if (editor.contentComponent) {
        return;
      }

      const element = this.editorContentRef;

      element.append(...Array.from(editor.options.element.childNodes));

      editor.setOptions({
        element,
      });

      editor.contentComponent = this;

      editor.createNodeViews();
    }
  }

  componentDidLoad() {
    this.init();
  }

  componentDidUpdate() {
    this.init();
  }

  disconnectedCallback() {
    const { editor } = this;

    if (!Boolean(editor)) {
      return;
    }

    if (!editor.isDestroyed) {
      editor.view.setProps({
        nodeViews: {},
      });
    }

    editor.contentComponent = null;

    if (!Boolean(editor.options.element.firstChild)) {
      return;
    }

    const newElement = document.createElement('div');

    newElement.append(...Array.from(editor.options.element.childNodes));

    editor.setOptions({
      element: newElement,
    });
  }

  render() {
    const { editor, ...rest } = this;

    return (
      <Fragment>
        <div ref={el => (this.editorContentRef = el)} {...rest} />
      </Fragment>
    );
  }
}
