import { Component, Prop, Element, Fragment, h } from '@stencil/core';
import tippy from 'tippy.js';

@Component({
  tag: 'tiptap-menu-item',
  styleUrl: 'tiptap-menu-item.scss',
})
export class TiptapMenuItem {
  @Element() el: HTMLTiptapMenuItemElement;

  /**
   * Menu item props.
   */
  @Prop() itemProps: any;

  private menuTippy?: any;
  private menuContent?: HTMLDivElement;
  private menuTippyTarget?: HTMLElement;

  private updateMenu() {
    if (this.menuTippy) {
      const { menuItems } = this.itemProps;
      this.menuContent = document.createElement('div');
      this.menuContent.classList.add('sub-menu');
      menuItems.forEach((item: any) => {
        const menuItem = document.createElement('tiptap-menu-item');
        menuItem.itemProps = { ...item, action: item.action ? () => {
          item.action();
          this.menuTippy.hide();
        } : null };
        this.menuContent.append(menuItem);
      });
      this.menuTippy.setContent(this.menuContent);
    }
  }

  componentDidLoad() {
    const { menuItems } = this.itemProps;
    if (menuItems) {
      this.menuTippy = tippy(this.menuTippyTarget ?? this.el, {
        placement: 'bottom-start',
        trigger: 'click',
        interactive: true,
        animation: false,
        appendTo: () => document.body,
      });
      this.updateMenu();
    }
  }

  componentDidUpdate() {
    if (this.menuTippy) {
      this.updateMenu();
    }
  }

  render() {
    const { icon, label, title, action, menuItems, isDisabled = null, isActive = null, flip = 'none' } = this.itemProps;

    return (
      <Fragment>
        <button class={`menu-item${isDisabled && isDisabled() ? ' is-disabled' : ''}${isActive && isActive() ? ' is-active' : ''}${flip !== 'none' ? ` flip-${flip}` : ''}${menuItems && action ? ' has-submenu' : ''}`} onClick={isDisabled && isDisabled() ? null : action} title={title}>
          {icon && ((icon as String).startsWith('ci') ? <i class={`coolicons ${icon}`} /> : <i class={icon} />)}
          {label && <span class="label">{label}</span>}
          {menuItems && !action ? <i class="coolicons ci-caret_down_sm" /> : null}
        </button>
        {menuItems && action && (
          <button class="menu-item" ref={el => (this.menuTippyTarget = el)}>
            <i class="coolicons ci-caret_down_sm" />
          </button>
        )}
      </Fragment>
    );
  }
}
