@import url('../../assets/coolicons/coolicons.css');

$colors: crimson, deeppink, darkorange, darkviolet, forestgreen, royalblue, saddlebrown, dimgray;

.menu-item {
  background-color: transparent;
  border: none;
  border-radius: 0.4em;
  color: #525252;
  height: 1.75em;
  margin-right: 0.25em;
  padding: 0.25em;
  min-width: 1.75em;
  cursor: pointer;

  i {
    font-size: 1.25em;
  }

  &.flip-h {
    transform: scaleX(-1);
  }

  &.flip-v {
    transform: scaleY(-1);
  }

  &.is-disabled {
    opacity: 0.5;
    cursor: unset;

    &:hover {
      background-color: transparent;
      color: #525252;
    }
  }

  &:hover,
  &.is-active {
    background-color: #525252;
    color: #fff;
  }

  &.has-submenu + & > i.ci-caret_down_sm {
    margin: 0 -0.25em;
  }

  &.has-submenu {
    margin-right: 0;
  }

  &.has-submenu:hover {
    border-radius: 0.4em 0 0 0.4em;
  }

  .has-submenu + & {
    border-radius: 0 0.4em 0.4em 0;
    margin-left: 0;
    padding: 0.25em 0;
    min-width: 0.75em;
  }

  tiptap-menu-item:hover &.has-submenu {
    border-radius: 0.4em 0 0 0.4em;
  }

  tiptap-menu-item:last-of-type & {
    margin-right: 0;
  }

  i.color {
    @each $color in $colors {
      &-#{"" + $color} {
        color: $color;
      }
    }
  }

  &:has(.color):hover,
  &:has(.color).is-active {
    background-color: transparent;
    box-shadow: inset 0 0 0 1px #525252;
  }
}

.sub-menu {
  padding: 0.1em;
  border-radius: 0.3em;
  background-color: #fff;
  border: 1px solid #a6a39e;
}
