import { Component, State, Prop, h } from '@stencil/core';
import { isTextSelection } from '@tiptap/core';
import { BubbleMenuPlugin, BubbleMenuPluginProps } from '@tiptap/extension-bubble-menu';
import { Editor } from '../../editor';

type Optional<T, K extends keyof T> = Pick<Partial<T>, K> & Omit<T, K>;
export type BubbleMenuProps = Omit<Optional<BubbleMenuPluginProps, 'pluginKey' | 'editor'>, 'element'>;

@Component({
  tag: 'tiptap-bubble-menu',
  styleUrl: 'tiptap-bubble-menu.scss',
})
export class TiptapBubbleMenu {
  /**
   * Bubble menu props.
   */
  @Prop() menuProps: BubbleMenuProps;

  /**
   * Tiptap editor instance.
   */
  @Prop() editor: Editor;

  /**
   * Whether the bubble menu should be disabled.
   */
  @Prop() disabled = false;

  @State() element: HTMLDivElement;

  componentDidLoad() {
    if (!Boolean(this.element) || this.editor.isDestroyed) {
      return;
    }

    const {
      pluginKey = 'bubbleMenu',
      tippyOptions = {},
      shouldShow = this.disabled ? () => false :({ view, state, from, to }) => {
        if ((view as any).input?.mouseDown) {
          return false;
        }
        const { doc, selection } = state;
        const isEmptyTextBlock = !Boolean(doc.textBetween(from, to).length) && isTextSelection(selection);
        if (!view.hasFocus() || selection.empty || isEmptyTextBlock) {
          return false;
        }
        return true;
      },
    } = this.menuProps;

    const plugin = BubbleMenuPlugin({
      pluginKey,
      editor: this.editor,
      element: this.element,
      tippyOptions,
      shouldShow,
    });
    this.editor.registerPlugin(plugin);
  }

  render() {
    const { editor } = this;
    const items = [
      {
        icon: 'ci-bold',
        title: 'Bold',
        action: () => editor.chain().focus().toggleBold().run(),
        isActive: () => editor.isActive('bold'),
      },
      {
        icon: 'ci-italic',
        title: 'Italic',
        action: () => editor.chain().focus().toggleItalic().run(),
        isActive: () => editor.isActive('italic'),
      },
      {
        icon: 'ci-strikethrough',
        title: 'Strike',
        action: () => editor.chain().focus().toggleStrike().run(),
        isActive: () => editor.isActive('strike'),
      },
      {
        icon: 'ci-code',
        title: 'Code',
        action: () => editor.chain().focus().toggleCode().run(),
        isActive: () => editor.isActive('code'),
      },
      {
        icon: 'ci-bulb',
        title: 'Highlight',
        action: () => editor.chain().focus().toggleHighlight().run(),
        isActive: () => editor.isActive('highlight'),
      },
    ];
    return (
      <div ref={el => (this.element = el)} class="bubble-menu" style={{ visibility: 'hidden' }}>
        {items.map((item, index) => {
          const { isHidden, type, ...rest } = item as any;
          if (isHidden && isHidden()) {
            return;
          }
          return type === 'divider' ? <div class="divider" key={index} /> : <tiptap-menu-item itemProps={{ ...rest }} key={index} />;
        })}
      </div>
    );
  }
}
